<template>
  <div class="list-card">
      <ul>
          <li v-for="(log,index) in data" :key="index" >
              <p>{{ log.description}}</p>
              <p class="ml-2">{{ setDate(log.date) }}</p>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    name : 'listCard',
    props : {
        data : Array,
    },
    data() {
        return {
            listMonth : [
                'Jan',
                'Feb',
                'Mar',
                'Apr',
                'May',
                'Jun',
                'Jul',
                'Aug',
                'Sept',
                'Oct',
                'Nov',
                'Des'
            ]
        }
    },
    methods : {
        setDate : function (date) {
            const epoch = date;
            const d = new Date(epoch);
            const minute = d.getMinutes();
            const hours = d.getHours();
            const time = d.getDate();
            const month = this.listMonth[d.getMonth()];
            const year = d.getFullYear();
            return `${hours}:${minute} ${time}-${month}-${year}`;
        }
    }
}
</script>

